@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);

body{background-color:#bbc5a1;margin:0;padding:0;line-height:3;font-family:"Rampart One", cursive, sans-serif;color:#836c04}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}.header{border-bottom:dashed 5px #446315;box-shadow:13px 22px 25px rgba(46,78,46,0.23);height:60px;width:100%}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto}.header nav div img{width:64px;position:absolute;top:3px}.header nav h1{float:right;margin-left:500px}.content{display:flex;max-width:922px;margin:auto;margin-top:100px}.sidebar{align-items:center;justify-content:center;display:grid;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:53vh;padding-top:2px;border-right:5px dashed #d4f4d4;background-color:rgba(224,250,241,0.33)}.sidebar div{margin-bottom:1px;cursor:pointer;transition-duration:0.4s}.sidebar div.active{background-color:#d4f4d4}.sidebar div:hover{font-size:0.7rem;color:black;background-color:#f1fff0;align-content:center;display:grid;justify-content:center}.add-task-btn:hover{cursor:pointer}.mainbar{margin-left:150px;width:1233px;height:88vh;background-color:#eaf7f2;border-left:2px solid #c2f3e3;border-right:2px solid #c2f3e3}.add-task-dialog{margin-top:10px}.add-task-dialog input{width:100%;border:1px dashed #aaa;background-color:aliceblue;border-radius:4px;height:35px;padding-left:10px;padding-right:10px}.add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.add-task-actions-container .add-btn{background-color:#aaa;border:2px solid transparent;color:azure;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:5px !important}.add-task-actions-container .add-btn:disabled{background-color:gray}.add-task-actions-container .add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.add-task-actions-container .cancel-btn{all:unset;color:#856;cursor:pointer;font-size:14px;margin:0px 5px}#print{float:right;margin-right:63px}

